export const getPointOnQuadraticBezier = (t, p0, p1, p2) => {
  let invT = 1 - t;
  return {
      x: invT * invT * p0.x + 2 * invT * t * p1.x + t * t * p2.x,
      y: invT * invT * p0.y + 2 * invT * t * p1.y + t * t * p2.y
  };
}


export const quadraticBezierPath = (p1, cp1, p2, segments = 50) => {
  const angleSegment = 1 / segments;

  const line = [];

  for (let i = 0; i <= segments; i++) {
      const t = angleSegment * i;

      line.push(
        getPointOnQuadraticBezier(t, p1, cp1, p2)
      );
  }

  return line;
}


export const getPointOnCubicBezier = (t, p0, p1, p2, p3) => {
  return {
      x: Math.pow(1 - t, 3) * p0.x + 3 * Math.pow(1 - t, 2) * t * p1.x + 3 * (1 - t) * Math.pow(t, 2) * p2.x + Math.pow(t, 3) * p3.x,
      x: Math.pow(1 - t, 3) * p0.y + 3 * Math.pow(1 - t, 2) * t * p1.y + 3 * (1 - t) * Math.pow(t, 2) * p2.y + Math.pow(t, 3) * p3.y
  };
}


export const cubicBezierPath = (p1, cp1, cp2, p2, segments = 50) => {
  const angleSegment = 1 / segments;
  const line = [];

  for (let i = 0; i <= segments; i++) {
      const t = angleSegment * i;

      line.push(
        getPointOnCubicBezier(t, p1, cp1, cp2, p2)
      );
  }

  return line;
}

export const linePath = (p1, p2, segments = 50) => {
  const slice = 1 / segments;
  const line = [];

  for (let i = 0; i <= segments; i++) {
    const t = slice * i;

    line.push(
      getPointOnLinePath(t, p1, p2)
    );
  }

  return line;
}

export const getPointOnLinePath = (t, p1, p2) => {
  return {
    x: p1.x + ((p2.x - p1.x) * t),
    y: p1.y + ((p2.y - p1.y) * t),
  };
}


export const rectPath = (tlx, tly, width, height) => {
  return [
      { x: tlx, y: tly },
      { x: tlx + width, y: tly },
      { x: tlx + width, y: tly + height },
      { x: tlx, y: tly + height },
  ];
}


export const circlePath = (cx, cy, radius, segments = 50, startingAngle = 0) => {
  return arcPath(cx, cy, radius, (Math.PI * 2), segments, startingAngle);
}


export const arcPath = (cx, cy, radius, angle, segments = 50, startingAngle = 0) => {
  const angleSegment = angle / segments;
  const angleOffset = startingAngle;
  const path = [];

  for (let i = 0; i <= segments; i++) {
    path.push(getPointOnArc(cx, cy, angleOffset + (angleSegment * i), radius));
  }

  return path;
}


export const getPointOnArc = (cx, cy, angle, radius) => {
  return {
    x: cx + (Math.cos(angle) * radius),
    y: cy + (Math.sin(angle) * radius)
  }
};
