import Engine from './utils/engine';
// import Engine from './utils/engine3d';

import tumult from 'tumult';
import { loadFont } from './utils/font';

import GothamFont from '../data/Gotham-Bold.ttf';
import { interpolate } from './utils/utils';

const WIDTH = 480;
const HEIGHT = 480;

const el = document.getElementById('canvas');
// const el = document.getElementById('container');

const app = new Engine(el, {
  debug: false,
  width: WIDTH,
  height: HEIGHT
});

function drawPoint(x, y, color = 'yellow') {
  ctx.save();
  ctx.fillStyle = color;
  ctx.translate(-1, -1);
  ctx.fillRect(x, y, 2, 2);
  ctx.restore();
}

function getPosition(y, t, height) {
  return interpolate(t, 0, y);
}

let path;
let box;

const letters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ'.split('');
let letterIdx = 0;
let xOffset = 0;

const noise = new tumult.Perlin1();

app.onRender(({ frame, width, height, debug }) => {
  ctx.fillStyle = 'black';
  ctx.fillRect(0, 0, width, height);

  if (frame % 60 == 0) {
    path = FontObj.getOptimisedPath(letters[letterIdx], 240);
    letterIdx++;

    if (letterIdx >= letters.length) {
      letterIdx = 0;
    }
  }

  const t = Math.sin(Math.PI * (frame / 60));

  ctx.save();
  ctx.strokeStyle = 'yellow';
  let wid = path.boundingBox.x2 - path.boundingBox.x1;
  ctx.translate(((width - wid) / 2) - 10, height / 2);

  // ctx.beginPath();
  // ctx.moveTo(path.points[0].x, getPosition(path.points[0].y, t + (noise.gen(0 / 10 + xOffset) / 5), height));
  // for (let i = 1; i < path.points.length; i++) {
  //   ctx.lineTo(path.points[i].x, getPosition(path.points[i].y, t + (noise.gen(i / 10 + xOffset) / 5), height));
  // }
  // ctx.stroke();
  for (let i = 0; i < path.points.length; i++) {
    drawPoint(path.points[i].x, getPosition(path.points[i].y, t + (noise.gen(i / 10 + xOffset) / 5), height));
  }

  xOffset += 0.1;
  xOffset = xOffset % 250;

  ctx.restore();
});

let FontObj;

loadFont(GothamFont)
  .then(Font => {
    FontObj = Font;

    app.start();
  });
