import * as opentype from 'opentype.js';
import { linePath, quadraticBezierPath } from './paths';

export class Font {
  constructor(fontPath) {
    this.fontPath = fontPath;
  }

  getPath(str, size, x = 0, y = 0) {
    return this.fontPath.getPath(str, x, y, size);
  }

  getOptimisedPath(str, size, x = 0, y = 0) {
    const path = this.fontPath.getPath(str, x, y, size);

    let points = [];
    let lastPoint = {};
    let lastMovePoint = {};

    for (let i = 0; i < path.commands.length; i++) {
      switch(path.commands[i].type) {
        case 'L':
          points = points.concat(
            linePath(
              lastPoint,
              {
                x: path.commands[i].x,
                y: path.commands[i].y,
              },
              5)
          );
          lastPoint = {
            x: path.commands[i].x,
            y: path.commands[i].y
          };
          break;
        case 'Q':
          points = points.concat(
            quadraticBezierPath(
              lastPoint,
              {
                x: path.commands[i].x1,
                y: path.commands[i].y1,
              },
              {
                x: path.commands[i].x,
                y: path.commands[i].y,
              },
              5)
          );
          lastPoint = {
            x: path.commands[i].x,
            y: path.commands[i].y
          };
          break;
        case 'M':
          lastPoint = {
            x: path.commands[i].x,
            y: path.commands[i].y
          };
          lastMovePoint = {...lastPoint};
          break;
        case 'Z':
          points.push({...lastMovePoint});
          lastPoint = {...lastMovePoint};
          break;
      }
    }

    return {
      boundingBox: path.getBoundingBox(),
      points
    };
  }
}

export function loadFont(fontPath) {
  return new Promise((resolve, reject) => {
    opentype.load(fontPath, (err, font) => {
      resolve(new Font(font));
    });
  });
}
